@import url(//db.onlinewebfonts.com/c/647a7208a3f353750207213a569815d4?family=Chronicle+Display);
@import url(//db.onlinewebfonts.com/c/9eea1ea594e51c3bd80ce21a298504b0?family=Stellar);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Chronicle Display Font */

/* Stellar Font */
.marker-btn {
    background: none;
    border: none;
    cursor: pointer;
}

.marker-btn img {
    width: 40px;
    height: 40px;
    background-color: white;
    border: 3px solid black;
    padding: 2px;
    border-radius: 50%;
}

.marker-btn:hover img {
    background-color: #aaaaaa;
}

.loader {
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.loader h1 {
    font-family: 'Chronicle Display', serif;
}
.popup {
    width: 400px;
    height: 400px;
    text-align: center;
    border: 2px solid #595959;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-evenly;
}

.mapboxgl-popup-close-button {
    font-size: 2rem;
    margin-top: 10px;
    margin-right: 18px;
}

.popup-name {
    font-family: 'Chronicle Display', serif;
    font-size: 1.75rem;
}

.popup-img-container {
    width: 200px;
    height: 200px;
    overflow: hidden;
    margin: 0 auto;
}

.popup-img {
    width: 100%;
    height: 100%;
    
}

.popup-type {
    font-family: 'Stellar', sans-serif;
    color: #595959;
    text-transform: lowercase;
}

.popup-moreInfo-btn {
    padding: 10px;
    font-family: 'Stellar', sans-serif;
    color: #595959;
}
header {
    height: 30px;
}

/* .selected {
    color: red;
} */

nav {
    background-color: #424B54;
    height: 30px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.item {
    text-decoration: none;
    color: whitesmoke;
    font-family: 'Stellar', sans-serif;
    text-transform: uppercase;
}


.page-container {
    background: whitesmoke;
    padding-top: 20px;
    height: 100vh;
}

.loader {
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.loader h1 {
    margin-bottom: 20px;
    margin-left: -20px;
    font-family: 'Chronicle Display', serif;
}

.custom-search-container {
    display: grid;
    grid-template-columns: 1fr 2fr;
    width: 70vw;
    margin: 0 auto;
    margin-top: 30px;
    height: 80vh;
    font-family: "Hack", monospace;
    border: 3px solid black; 
    box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000, 5px 5px 15px 5px rgba(230,232,230,0);
}

.left-container {
    grid-column: 1 / span 1;
    /* background: #344966; */
    background-image: url(/static/media/vertical-food-banner.36bb5cc0.jpg);
    background-size: cover;
    background-position: right;
    display: flex;
    justify-content: center;
    text-shadow: #474747 3px 5px 2px, 2px 2px 2px rgba(52,73,102,0);
}



.title-container i {
    font-size: 2rem;
    display: inline-block;
}

.title-container h1 {
    font-size: 2rem;
    display: inline-block;
    margin-left: 15px;
    font-family: 'Chronicle Display', serif;
}


.right-container {
    grid-column: 2 / span 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color: black;
    background: white;
    text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);
    font-family: 'Stellar', sans-serif;
    text-align: center;
}

.custom-form > input {
    display: block;
    margin: 20px auto;
    width: 150px;
}

.all-results {
    background: whitesmoke;
}

.num-results-display {
    font-size: 2rem;
    margin: 50px;
    font-family: 'Stellar', sans-serif;
}

.result-container {
    text-align: center;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: whitesmoke;
}

.result-title {
    font-size: 3rem;
    font-family: 'Chronicle Display', serif;
    letter-spacing: 1rem;
    margin: 15px;
}

.result-img-container {
    width: 800px;
    height: 500px;
    overflow: hidden; 
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
}

.result-img {
    width: 100%;
    height: 100%;
}

.open-status {
    margin: 50px 10px;
    font-size: 2rem;
    font-family: 'Stellar', sans-serif;
}

.open-span {
    color: #FFFFFF;
    background: #912C22;
    text-shadow: 0 0 10px #FFFFFF;
    padding: 2px 5px;
    border: 3px solid black;
}

.location {
    margin: 20px 10px;
    font-size: 2rem;
    font-family: 'Stellar', sans-serif;
}

.address-div {
    font-size: 1.5rem;
    line-height: 2rem;
    font-family: 'Stellar', sans-serif;
}

.website-link {
    width: 200px;
    height: 50px;
    background-color: white;
    border: 3px solid #595959;
    border-radius: 3%;
    color: #595959;
    padding: 15px;
    margin: 20px;
    font-family: 'Stellar', sans-serif;
}

.website-link:hover {
    background-color: #595959;
    color: white;
}

hr {
    height: 5px;
    color: black;
    width: 100vw;
    margin-top: 15px;
}
#search-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.intro-modal-container {
    display: flex;
    flex-flow: column;
    justify-content: center;
    height: 80vh;
}


.title-logo {
    width: 500px;
    margin-bottom: 50px;
}

.start-btn {
    background: none;
    border: none;
    cursor: pointer;
    margin: 15px;
    font-family: 'Stellar', sans-serif;
}

.start-btn img {
    width: 60px;
    height: 60px;
    padding-top: 10px;
    margin-bottom: 10px;
}

.start-btn:focus {
    outline: 0;
}

.start-btn:hover {
    color: gray;
    /* I kept background-color as a future design idea */
    /* background-color: rgba(0,0,0,0.1); */
}

.location-modal-container {
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    align-items: center;
    height: 80vh;
}

.location-h1 {
    font-family: 'Chronicle Display', serif;
}

.location-or {
    font-size: 1.4rem;
    font-family: 'Chronicle Display', serif;
}


.search-by-location-btn {
    font-family: 'Stellar', sans-serif;
    background: #93A8AC;
    color: #1d1d1d;
    cursor: pointer;
    font-size: 2em;
    padding: 1.5rem;
    border: 0;
    transition: all 0.5s;
    border-radius: 10px;
    position: relative;
    width: 360px;
}
.search-by-location-btn::after {
        content: "\f124";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        position: absolute;
        top: 54%;
        right: 0px;
        bottom: 0;
        opacity: 0;
        transform: translate(-50%, -50%);

    }

.search-by-location-btn:hover {
        background: #2b2bff;
        transition: all 0.5s;
        border-radius: 10px;
        box-shadow: 0px 6px 15px #0000ff61;
        padding: 1.5rem 4rem 1.5rem 1.5rem;
        color: #ffffff;
        width: 400px;
}
.search-by-location-btn:hover::after {
            opacity: 1;
            transition: all 0.5s;
            color: #ffffff;

        }

.back-btn {
    font-family: "Stellar", sans-serif;
    background: #93A8AC;
    color: #1d1d1d;
    cursor: pointer;
    font-size: 2em;
    padding: 1.5rem;
    border: 0;
    transition: all 0.5s;
    border-radius: 10px;
    width: 360px;
    display: block;
    text-decoration: none;
    margin: 0 auto;
    margin-top: 20px;
}

.back-btn:hover {
    background: #2b2bff;
    color: #ffffff;
}

a {
    text-decoration: none;
}
    




